import React from 'react'

function Post({ post }) {
	return (
		<div className="post">
			<div className="author-avatar">
				<img src={post.author.photo} alt={post.author.photo} />
			</div>
			<header>
				<span className="author-name">{post.author.name}</span>
				<span className="author-nickname">{post.author.nickname}</span>
				<span className="post-date">{post.date}</span>
			</header>
			<div className="post-content">
				<div className="post-content-text">{post.textContent}</div>
				<div className="post-content-image">
					<img src={post.content.imageContent} alt="Content" />
				</div>
			</div>
			<footer>
        <div className="post-actions">
          <div className="action-comment">
            <img src={post.action.comment.icon} alt="Comment icon" />
            <span className="comment-counter">{post.action.comment.counter}</span>
          </div>
        </div>
      </footer>
		</div>
	)
}

export default Post
